.btn-slide {
    width: 60px;
    height: 60px;
    border-radius: 50%;
    background: #f1f1f1;
    border: 1px solid rgba(34, 34, 34, 0.287);
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
  }
  
  .btn-slide img {
    width: 20px;
    height: 20px;
    pointer-events: none;
  }
  
  .prev {
    top: 50%;
    left: 20px;
    transform: translateY(-60%);
  }
  
  .next {
    top: 50%;
    right: 20px;
    transform: translateY(-60%);
  }